.card-container {
  display: flex;
  flex-wrap: wrap;
  margin-left: 20rem;
  margin-top: 2rem;
  z-index: -2;
}

.card {
  margin: 20px;
  border: 2px solid #ededed;
  padding: 20px;
  cursor: pointer;
}

.card-img {
  width: 13rem;
  margin-bottom: 1rem;
}

.card-title {
  margin-bottom: 1rem;
}

.card-reviews {
  margin-bottom: 1rem;
  display: flex;
}

.rating-star {
  color: #d5ab55;
}

.total-reviews {
  font-size: 0.9rem;
  margin-left: 10px;
}

.card-price {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bag-icon {
  color: #535353;
}
@media (max-width: 600px) {
  
    .card-container {
      display: flex;
      flex-wrap: wrap;
      margin-left: 10rem;
      margin-top: 2rem;
      z-index: -2;
    }
    .card-img {
      width: 7rem;
      margin-bottom: 1rem;
    }
  }

  @media (max-width: 400px) {
  
    .card-container {
      display: flex;
      flex-wrap: wrap;
      margin-left: 10rem;
      margin-top: 2rem;
      z-index: -2;
    }
    .card-img {
      width: 7rem;
      margin-bottom: 1rem;
    }
    .card {
      margin: 10px;
      border: 2px solid #ededed;
      padding: 20px;
      cursor: pointer;
    }
  }
  @media (min-width: 1400px) {
  
    .card-container {
      display: flex;
      flex-wrap: wrap;
      margin-left: 20rem;
      margin-top: 2rem;
      z-index: -2;
    }
    .card-img {
      width: 7rem;
      margin-bottom: 1rem;
    }
    .card {
      margin: 20px;
      border: 2px solid #ededed;
      padding: 20px;
      cursor: pointer;
    }
  }


 